<template>
    <div :class="'pill ' + computedClass">{{ urgency }}</div>
</template>
  
<script>
export default {
    props: {
        urgency: {
            type: String,
            required: true,
            validator: (value) => ['Low', 'Medium', 'High', 'Open', 'Closed'].includes(value)
        }
    },
    computed: {
        computedClass() {
            switch (this.urgency) {
                case 'Low':
                case 'Closed':
                    return 'low';
                case 'Medium':
                case 'Open':
                    return 'medium';
                case 'High':
                    return 'high';
                default:
                    return '';
            }
        }
    }
};
</script>
  
<style>
.pill {
  display: inline-block;
  padding: 0.25rem 0.5rem;
  border-radius: 2rem;
  font-size: 0.7rem;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
}

/* Urgency styles */
.low {
    background-color: #e6f7ff;
    /* Light blue */
    color: #087700;
    /* Blue */
    font-weight: bold;
}

.medium {
    background-color: #fffbe6;
    /* Light yellow */
    color: #faad14;
    /* Orange */
    font-weight: bold;
}

.high {
    background-color: #fff1f0;
    /* Light red */
    color: #f5222d;
    /* Red */
    font-weight: bold;
}</style>